.summary-block {
  background: #ffffff;
  border-radius: 12px;
  padding: 28px;
  box-shadow:
    0 2px 8px rgba(0, 0, 0, 0.08),
    0 8px 24px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
  border: 1px solid #e5e7eb;
  border-left: 4px solid transparent;

  &:hover {
    box-shadow:
      0 4px 16px rgba(0, 0, 0, 0.15),
      0 12px 32px rgba(0, 0, 0, 0.1);
    border-color: #d1d5db;
  }
}

.summary-header {
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--surface-100);
}

.summary-title {
  color: var(--text-color);
  font-size: 1.375rem;
  font-weight: 700;
  margin: 0;
  letter-spacing: -0.25px;
}

.summary-narrative {
  margin-bottom: 24px;

  p {
    color: var(--text-color-secondary);
    line-height: 1.7;
    margin: 0;
    font-size: 1rem;
  }
}

.highlights {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 16px;
}

.highlight-item {
  background: #f9fafb;
  border-radius: 10px;
  padding: 18px;
  position: relative;
  transition: all 0.2s ease;
  border: 1px solid #e5e7eb;
  overflow: hidden;

  &:hover {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    border-color: #d1d5db;
    background: #f3f4f6;
  }
}

.highlight-content {
  position: relative;
  z-index: 2;
}

.highlight-value {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 1.625rem;
  font-weight: 700;
  color: var(--text-color);
  margin-bottom: 6px;

  i {
    font-size: 1rem;
    transition: transform 0.2s ease;
  }
}

.highlight-label {
  font-size: 0.8125rem;
  color: var(--text-color-secondary);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.highlight-indicator {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  transition: width 0.3s ease;
}

.highlight-item:hover .highlight-indicator {
  width: 8px;
}

// Trend Icons
.trend-up {
  color: #22c55e;
  &::before {
    content: '\2191';
  }
}

.trend-down {
  color: #ef4444;
  &::before {
    content: '\2193';
  }
}

.trend-stable {
  color: #6b7280;
  &::before {
    content: '\2192';
  }
}

// Severity Colors
.severity-good .highlight-indicator {
  background: linear-gradient(180deg, #22c55e, #16a34a);
}

.severity-warning .highlight-indicator {
  background: linear-gradient(180deg, #f59e0b, #d97706);
}

.severity-critical .highlight-indicator {
  background: linear-gradient(180deg, #ef4444, #dc2626);
}

.severity-neutral .highlight-indicator {
  background: linear-gradient(180deg, #6b7280, #4b5563);
}

// Theme Variants
.summary-block.theme-primary {
  border-left-color: #3b82f6;
}

.summary-block.theme-warning {
  border-left-color: #f59e0b;
}

.summary-block.theme-danger {
  border-left-color: #ef4444;
}

.summary-block.theme-success {
  border-left-color: #22c55e;
}

.summary-block.theme-info {
  border-left-color: #06b6d4;
}

// Dark Mode Support
:host-context(.dark) {
  .summary-block {
    background: var(--surface-800);
    border-color: var(--surface-600);
  }

  .highlight-item {
    background: var(--surface-700);
    border-color: var(--surface-600);
  }
}

// Responsive Design
@media (max-width: 768px) {
  .summary-block {
    padding: 16px;
  }

  .highlights {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .summary-title {
    font-size: 1.25rem;
  }

  .highlight-value {
    font-size: 1.5rem;
  }
}
